@import '../../../global.scss';

.add-form {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

    @include mobile {
        width: 100vw;
        margin-left: -0.75rem;
    }

    .add-form-container {
        height: 100%;
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .form {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: space-evenly;

            .form-container {
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-evenly;

                h1 {
                    font-size: 2.75rem;
                    color: white;
                }

                .radios {
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-left: 1rem;

                    .radioset {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-right: 1rem;

                        input[type='radio'] {
                            height: 1.5rem;
                            width: 1.5rem;
                            margin-right: 0.25rem;
                        }

                        .radio-label {
                            font-size: 1.5rem;
                            font-weight: 300;
                            color: white;
                        }
                    }
                }
            
                .fieldset {
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: center;

                    @include mobile {
                        align-items: center;
                    }
            
                    input[type='text'] {
                        height: 4rem;
                        width: 50%;
                        background-color: $gunmetal;
                        border: none;
                        border-bottom: 2px solid white;
                        margin-bottom: 2rem;
                        padding-left: 1rem;
                        font-size: 2rem;
                        color: white;

                        @include mobile {
                            width: 90%;
                        }

                        &::placeholder {
                            font-size: 2rem;
                            color: white;
                        }
                    }

                    #textarea {
                        height: 15rem;
                        width: 100%;
                        background-color: $gunmetal;
                        border: 2px solid white;
                        border-radius: 20px;
                        padding: 1rem 0 0 1rem;
                        font-size: 1.125rem;
                        color: white;
                        font-weight: 300;

                        @include mobile {
                            width: 90%;
                        }

                        &::placeholder {
                            font-size: 1.125rem;
                            color: white;
                        }
                    }
                }

                .bottom-btn-group {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .note-btn {
                        height: 2.5rem;
                        width: 5rem;
                        border-radius: 50px;
                        background-color: $gunmetal;
                        font-size: 1rem;
                        margin-right: 1rem;
                        cursor: pointer;
                        transition: all 0.5s ease;
                        text-decoration: none;
                    }

                    .close {
                        border: 2px solid $babyBlue;
                        color: $babyBlue;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        &:hover {
                            background-color: $babyBlue;
                            color: $gunmetal;
                        }
                    }

                    .save {
                        height: 2.75rem;
                        border: 2px solid $mauve;
                        color: $mauve;

                        &:hover {
                            background-color: $mauve;
                            color: $gunmetal;
                        }
                    }
                }
            }
        }
    }
}

